* {
	box-sizing: border-box; }

.cf:before,
.cf:after {
	content: "";
	display: block; }
.cf:after {
	clear: both; }
.cf {
	zoom: 1; }
a {
	color: inherit;
	text-decoration: none; }
.btn-default {
	border-radius: 5px;
	display: inline-block; }
.btn-border {
	border: 1px solid #fff;
	@include rmin(1200) {
		transition: all .4s;
		position: relative;
		&:hover {
			box-shadow:  0 0 2px #fff; } } }
.inner-wrapper {
	@include rmin(1200) {
		padding:  0 22px; } }
